    margin-top: 2mm;
    /* margin-left: 5mm; */
  }

  .pass-card {
    font-weight: bold;
    font-size: 4.5mm;
    background-color: #6b0f10;
    color: #fcf7f1;
    text-align: center;
    height: 8.8mm;
