    background-color: white;
    width: fit-content;
    text-align: center;
  } */

  .code-pass {
    font-family: FXMatrix105MonoPica;
    font-size: 3.3mm !important;
  }

  .code-on-ticket-pass {
    font-family: FXMatrix105MonoPica;
    font-size: 1.4mm !important;
    /* position: static; */
    /* transform: rotate(90deg); */
    margin-top: 1.5mm;
    /* margin-left: 5mm; */
    top: 2mm;
    right: 5mm;
    float: inherit;
    text-align: center;
    padding: 0.5mm;
    /* width: 52mm; */
    /* padding-top: 50mm; */
  }

  .line-pass {
    /* border-style: dotted; */
    border-width: 0.1mm;
  }

  .mt-2 {
    margin-top: 1mm;
  }
  .size10 {
    font-size: 1mm;
  }
  .bold {
    font-weight: bold;
  }

  .corner-left-top-element {
    width: 30mm;
    height: 30mm;
    position: absolute;
    opacity: 40%;
  }

  .corner-righ-bottom-element {
    width: 30mm;
    height: 30mm;
    /* position: absolute; */
    opacity: 40%;
    transform: rotate(180deg);
    bottom: 0;
    right: 0;
  }
  .darurat {
    font-family: Roboto;
